    --background: #94a3b8;
    --header-background: #64748b;
    --hover-button: #8492a5;
    --green-button: rgb(66, 209, 66);
    --red-button: rgb(253, 61, 61);
}

html, body {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: Verdana, Geneva, Tahoma, Georgia, sans-serif;
    background-color: var(--background);
    color: white;
}

h1, h3, p {
    margin: 0;
    padding: 0;
}

#container {
    min-height: 100%;
    display: grid;
    grid-template-columns: 200px 5fr;
    grid-template-rows: 80px 5fr 30px;
}

.header {
    background-color: var(--header-background);
    grid-row: 1;
    grid-column: 1 / 3;
    box-shadow: 0px -3px 10px 5px white;
    padding: 20px;
    border-radius: 15px;
}

.side-bar {
    grid-row: 2 / 4;
    grid-column: 1 / 2;
    padding: 20px;
}

.default-view {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 20px;
}

button {
    cursor: pointer;
    background-color: var(--background);
    color: white;
    border: none;
    font-size: 1rem;
    text-align: left;
    padding: 10px;
}

.homeBtn:focus,
.todayBtn:focus,
.weekBtn:focus,
.homeBtn:hover,
.todayBtn:hover,
.weekBtn:hover {
    background-color: var(--hover-button);
    border-radius: 5px;
    font-weight: bold;
}

.project-view {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.project-view > h3 {
    font-size: 1.5rem;
    border-bottom: 1px solid white;
    padding-bottom: 10px;
}

#project-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.project-select {
    font-size: 1rem;
    display: flex;
    justify-content: space-between;
    padding: 10px 4px;
}

.project-select:hover,
.project-selected {
    cursor: pointer;
    background-color: var(--hover-button);
    border-radius: 5px;
    font-weight: bold;
}

.addProjectBtn,
.add-task-btn {
    font-weight: bold;
    font-size: 1rem;
    padding: 8px 10px;
}

.fa-plus {
    color: var(--red-button);
}

.addProjectBtn:hover,
.add-task-btn:hover {
    background-color: var(--hover-button);
    border-radius: 5px;
}


.add-project-input,
.add-task-input,
.task-due-date,
.todo-edit-name,
.edit-due-date {
    background-color: var(--hover-button);
    padding: 5px;
    width: 150px;
    border-radius: 5px;
    border: none;
    color: white;
    font-size: 1rem;
}

.project-btn-field {
    display: flex;
    justify-content: space-around;
}

.project-add-btn,
.project-cancel-btn,
.task-add-btn,
.task-cancel-btn,
.confirm-edit,
.cancel-edit {
    text-align: center;
    padding: 6px 10px;
    margin: 0;
    width: 70px;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: bold;
}

.confirm-edit,
.cancel-edit {
    width: 100px;
}

.project-add-btn,
.project-add-btn:hover,
.task-add-btn,
.task-add-btn:hover,
.confirm-edit,
.confirm-edit:hover {
    background-color: var(--green-button);
}

.project-cancel-btn,
.project-cancel-btn:hover,
.task-cancel-btn,
.task-cancel-btn:hover,
.cancel-edit,
.cancel-edit:hover {
    background-color: var(--red-button);
}

.project-tab {
    width: 150px;
    display: flex;
    justify-content: space-between;
}

.main {
    border-left: 2px solid rgba(256 256 256 / 0.7);
}

#main-content {
    margin: 30px 150px;
    
}

.todo-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-bottom: 20px;
}

.todo-item {
    display: flex;
    justify-content: space-between;
    font-size: 18px;
    border: 1px solid white;
    padding: 10px 20px;
    box-sizing: border-box;
    height: 50px;
    align-items: center;
    border-radius: 5px;
}

.todo-item:hover {
    box-shadow: 0px 0px 5px 1px white;
}

.todo-left-side,
.todo-right-side {
    display: flex;
    gap: 12px;
    align-items: center;
}

.add-task-input {
    height: 21px;
}

.todo-right-edit {
    display: flex;
    gap: 10px;
}

.todo-edit-name {
    display: flex;
    width: 150%;
}

#display-add-project {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

#display-add-task {
    display: flex;
    justify-content: space-between;
    border: 1px solid white;
    align-items: center;
    padding: 10px 20px;
    border-radius: 5px;
}

#display-add-task:hover {
    box-shadow: 0px 0px 5px 1px white;
}

.add-task-input {
    display: flex;
    width: 50%;
}

#display-add-task.hide-input,
#display-add-project.hide-input {
    display: none;
}

.hide-btn-active,
.hide-input,
.edit-view-active,
.default-view-active {
    display: none;
}

/* Footer support */
.footer {
    grid-column: 2;
    grid-row: 3;
    display: flex;
    border-left: 2px solid rgba(256 256 256 / 0.7);
    justify-content: center;
}

.footer-content {
    display: flex;
    gap: 10px;
    font-size: 1rem;
    color: black;
    align-items: end;
}

.fa-github {
    font-size: 20px;
    color: black;
    transition: transform 300ms ease-out;
}

.fa-github:hover {
    transform: translate(0px, -5px) scale(1.5);
    color: purple;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,qBAAqB;IACrB,4BAA4B;IAC5B,uBAAuB;IACvB,gCAAgC;IAChC,8BAA8B;AAClC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,SAAS;IACT,UAAU;IACV,yDAAyD;IACzD,mCAAmC;IACnC,YAAY;AAChB;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,gCAAgC;IAChC,iCAAiC;AACrC;;AAEA;IACI,0CAA0C;IAC1C,WAAW;IACX,kBAAkB;IAClB,mCAAmC;IACnC,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,mCAAmC;IACnC,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,gBAAgB;IAChB,aAAa;AACjB;;AAEA;;;;;;IAMI,qCAAqC;IACrC,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,8BAA8B;IAC9B,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,aAAa;IACb,8BAA8B;IAC9B,iBAAiB;AACrB;;AAEA;;IAEI,eAAe;IACf,qCAAqC;IACrC,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;;IAEI,iBAAiB;IACjB,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;;IAEI,qCAAqC;IACrC,kBAAkB;AACtB;;;AAGA;;;;;IAKI,qCAAqC;IACrC,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,6BAA6B;AACjC;;AAEA;;;;;;IAMI,kBAAkB;IAClB,iBAAiB;IACjB,SAAS;IACT,WAAW;IACX,kBAAkB;IAClB,eAAe;IACf,iBAAiB;AACrB;;AAEA;;IAEI,YAAY;AAChB;;AAEA;;;;;;IAMI,qCAAqC;AACzC;;AAEA;;;;;;IAMI,mCAAmC;AACvC;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,8CAA8C;AAClD;;AAEA;IACI,kBAAkB;;AAEtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,eAAe;IACf,uBAAuB;IACvB,kBAAkB;IAClB,sBAAsB;IACtB,YAAY;IACZ,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,iCAAiC;AACrC;;AAEA;;IAEI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;IACb,WAAW;AACf;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,iCAAiC;AACrC;;AAEA;IACI,aAAa;IACb,UAAU;AACd;;AAEA;;IAEI,aAAa;AACjB;;AAEA;;;;IAII,aAAa;AACjB;;AAEA,